
import React from "react"
import axios  from "axios"





 class Login extends React.Component{
     constructor(){
         super()
         this.state  = {
             error:null
         }
     }
     user = {}
   getEmail = (event)=>{
       this.user.email = event.target.value
   }
   getPassword = (event)=>{
   this.user.password = event.target.value
   }
   getName = (event)=>{
    this.user.name = event.target.value
    }

 

   


   login = ()=>{
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
             if(re.test(String(this.user.email).toLowerCase())){
                       axios({
                           method:'post',
                           url:"/api/user/login",
                           data:this.user
                       }).then((response)=>{
                           console.log("response from api" , response)
                           if(response["data"]["email"]){
                               localStorage.email = response["data"]["email"]
                               this.props.history.push('/home')
                           }
                       } , (error)=>{
                           console.log("error from login api" , error)
                       })
             }
             else{
                 alert("In Valid Email Syntax")
             }
            
   }
    render(){
        return (
            <div>
                
                <div className="center">
                <h1>Login Here </h1>
                <input onChange={this.getEmail} placeholder="Email" type="email" className="form-control"></input>
                <input onChange={this.getPassword} placeholder="Password" type="password" className="form-control"></input>
                <label>{this.state.error}</label>
                <button className="btn btn-primary" onClick={this.login}>Login</button>
            </div>
            </div>
        )
    }
 
}

export default Login

