
import React from "react"
import axios  from "axios"
import {Redirect} from "react-router-dom"





 class Signup extends React.Component{
     constructor(){
         super()
         this.state  = {
             error:null
         }
     }
     user = {}
   getEmail = (event)=>{
       this.user.email = event.target.value
   }
   getPassword = (event)=>{
   this.user.password = event.target.value
   }
   getName = (event)=>{
    this.user.name = event.target.value
    }
   register = ()=>{
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
             if(re.test(String(this.user.email).toLowerCase())){
                       alert("Valid Email Syntax")
                       axios({
                           method:'post',
                           url:"/api/user/register",
                           data:this.user
                       }).then((response)=>{
                           console.log("response from api" , response)
                       } , (error)=>{
                           console.log("error from login api" , error)
                       })
             }
             else{
                 alert("In Valid Email Syntax")
             }
            
   }
    render(){
        return (
            <div className="center">
                <h1>Register Here </h1>
                <input onChange={this.getEmail} placeholder="Email" type="email" className="form-control"></input>
                <input onChange={this.getName} placeholder="Name" type="email" className="form-control"></input>
               
                <input onChange={this.getPassword} placeholder="Password" type="password" className="form-control"></input>
                <label>{this.state.error}</label>
                <button className="btn btn-primary" onClick={this.register}>Register</button>
                
              
            </div>
        )
    }
 
}

export default Signup

