
import React from "react"
import axios  from "axios"




 class PlayVideo extends React.Component{
     constructor(){
         super()
         this.state  = {
             videourl:null
         }
     }
     componentDidMount(){
         this.setState({
             videourl:'/api/playvideo?filename='+this.props.match.params.video
         })
     }
    render(){
        return (
            <div>
                
                <h1>Playing your video </h1>
           {this.state.videourl &&      <video type="video/m4v" controls src={this.state.videourl}></video> }
            </div>
        )
    }
 
}

export default PlayVideo



