import React , {Component} from "react"
import Carousel from "./Carousel"
import Video from "./Video"
import axios from "axios"


export default class Home extends Component {

    constructor(){
        super()
        this.state = {
            movies:[]
        }
    }
    componentDidUpdate(){
    }
    componentDidMount(){
        if(!localStorage.email){
            this.props.history.push('/register')
        }
        axios({
            method:'get',
            url:"/api/allassets"
        }).then((response)=>{
            console.log("response from api" , response) // ["" , "" , ""]
            var movies = []
            response.data.videos.forEach(filename => {
                 var obj = {
                     title:filename.split(".")[0],
                     name:filename,
                     image:"defaultvideo.jpg",
                     imdb:7.0
                 }
                 movies.push(obj)
                 console.log(".................... some file like data.js" , movies)
                 
            });
            this.setState({
                movies:movies
            })
        } , (error)=>{
            console.log("error from api " , error)
        })
    }
    // componentWillUnmount(){
    //     alert()
    // }
    render(){
        return(
        <div>
            <Carousel />
      <div className="row">
      {this.state.movies.map(function(eachmovie){
        return <Video data={eachmovie} />
      })}
      </div>
        </div>)
    }
}

// component did mount is a life cycle method which tell u taht component is successfully mounted in dom

