import {Link} from "react-router-dom"


export default function(props){
    return (
        <div class="card" style={{ width:"18rem"}}>
  <img src="download.jpeg" class="card-img-top" alt="..."/>
  <div class="card-body">
    <h5 class="card-title">{props.data.name}</h5>
    <p class="card-text">Friends is Rom-com series of US</p>
     <Link to={"/playvideo/"+props.data.name}> <a class="btn btn-primary">Play</a> </Link>
  </div>
</div>
    )
}



// Everycomponent can receive props , props is an object 
// in function we have caught props in props variable
// we have used movie name as props.name 
// Similarly we can use images imbd duration , anything