import {Link} from "react-router-dom"
function Navbar(){
    return(
    <nav class="navbar navbar-expand-lg navbar-light bg-light">
  
    <div class="container-fluid">
    <Link to="/"> <a class="navbar-brand">My Video Streaming</a> </Link>
      <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav me-auto mb-2 mb-lg-0">
           
         
        
          <li class="nav-item dropdown">
            <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
              My Account
            </a>
            <ul class="dropdown-menu" aria-labelledby="navbarDropdown">
              <li><a class="dropdown-item" href="#">Delete Account</a></li>
              <li><hr class="dropdown-divider"></hr></li>
              <li><a class="dropdown-item" href="#">Something else here</a></li>
            </ul>
          </li>
          <li class="nav-item">
           <input class="form-control" type="search" placeholder="Search" aria-label="Search"></input>
           </li>
           <li class="nav-item"><button class="btn btn-outline-primary">Search</button></li>
        
        </ul>
        <form class="d-flex">
      
         <Link to="/login"> <button class="btn btn-success" type="submit">Login</button></Link>
        <Link to="/register">  <button class="btn btn-success" type="submit">Signup</button></Link>

        </form>
      </div>
    </div>
  </nav>)
}

export default Navbar